.navSpan {
  color: rgb(255, 255, 255);
  position: relative;
  display: block;
  cursor: pointer;
}

.navSpan:before, span:after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  top: 50%;
  margin-top: -0.5px;
  background: rgb(134, 134, 134);
}

.navSpann:before {
  left: -2.5px;
}
.navSpan:after {
  right: 2.5px;
  background: rgb(255, 255, 255);
  transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.navSpan:hover:before {
  background: rgb(255, 255, 255);
  width: 100%;
  transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.navSpan:hover:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

